"use client";

import WSButton from "@/app/UI/WSButton/WSButton";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@radix-ui/react-avatar";
import {
  ArrowRight,
  ArrowUpRight,
  ChevronRight,
  Circle,
  Link,
} from "lucide-react";

import { motion } from "framer-motion";
import { Fragment, useEffect, useState } from "react";
import ServiceCard from "@/app/components/homeComps/ServiceCard";
import { Logos } from "../../components/homeComps/logos";
import Experts from "@/app/components/homeComps/Experts";
import { backgrounds } from "@/app/components/homeComps/Backgrounds";
import Industries from "@/app/components/homeComps/industries";
import { teamData } from "@/app/components/homeComps/TeamCard";
import {
  FaRegArrowAltCircleLeft,
  FaRegArrowAltCircleRight,
} from "react-icons/fa";
import BlogCard from "@/app/components/homeComps/BlogCard";
import Blog from "../blog/page";

export default function Hero() {
  // state for logo switch
  let [bgIndex, setBgIndex] = useState(0);

  // experts
  const [experts, setExperts] = useState("the composable stack.");

  // Teams
  const [teamIndex, setTeamIndex] = useState(0);
  // backgrounds
  const bg = backgrounds;

  const [animateMobile, setAnimateMobile] = useState("");
  useEffect(() => {
    const handleResize = () => {
      setAnimateMobile(window.innerWidth <= 768);
    };

    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  // const bg = '/images/Masonary/m-1.webp'

  return (
    <Fragment>
      {/* hero */}
      {bg.map((bg, index) => {
        if (bgIndex === index) {
          return (
            <section
              key={index}
              style={{ backgroundImage: `url(${bg.bg})` }}
              className={` bg-${bg.bgColor} bg-cover bg-center  bg-no-repeat flex flex-col items-center justify-center p-10 lg:px-[5%] lg:px-12`}
            >
              <div className="  w-full max-lg:space-y-10 flex max-lg:flex-row   grid lg:grid-cols-2  items-center">
                {/* Left Text Content */}
                <div className=" flex flex-col h-full max-lg:space-y-4  justify-evenly max-lg:pt-[50px]  lg:px-[5%]">
                  <h1
                    className={`text-5xl max-lg:text-[20px] max-lg:text-center font-bold leading-tight text-${bg.textColor} `}
                  >
                    We craft fast, scalable sites for teams of all sizes.
                  </h1>

                  <h4
                    className={`font-normal max-lg:text-center text-${bg.textColor} text-[20px] max-lg:text-[14px] lg:leading-[30px] `}
                  >
                    Webstacks empowers marketing teams to break free from
                    website bottlenecks with a composable approach built for
                    speed and scale.
                  </h4>

                  <div className="max-lg:space-y-2 lg:flex lg:gap-2 lg:items-center">
                    <WSButton
                      icon={<ArrowUpRight />}
                      value="Book Intro Call"
                    ></WSButton>
                    <Button className="lg:p-6 text-base flex items-center gap-2 lg:text-[16px] max-lg:text-[14px]  max-lg:w-full max-lg:p-6">
                      View Client Stories
                      <ArrowRight className="w-5 h-5 max-lg:w-2 max-lg:h-2 text-white" />
                    </Button>
                  </div>
                </div>

                {/* Right Image */}
                <motion.div
                  initial={{ rotateX: 0, rotateY: 0 }}
                  animate={{ rotateX: 360, rotateY: 360 }}
                  transition={{ duration: 2, ease: "easeInOut" }}
                  className="border rounded-[10px] h-[80vh] lg:w-full bg-white overflow-hidden"
                >
                  <div className=" flex flex-row text-[10px] h-[36px] border-b-1 justify-between items-center bg-white px-4 rounded-t-sm">
                    <div className="flex flex-row space-x-1 ">
                      <Circle
                        className={`lg:w-4 lg:h-4 max-lg:w-3 max-lg:h-3 bg-gray-200 text-gray-200 rounded-[10px]`}
                      />
                      <Circle className="w-4 h-4 max-lg:w-3 max-lg:h-3 bg-gray-200 text-gray-200 rounded-[10px]" />
                      <Circle className="w-4 h-4 max-lg:w-3 max-lg:h-3 bg-gray-200 text-gray-200 rounded-[10px]" />
                    </div>
                    <div className="flex justify-center   w-full">
                      <Link />
                    </div>
                  </div>
                  <div className="overflow-hidden w-full flex flex-col justify-center  scrollbar-hide">
                    <Avatar className="w-full h-[calc(80vh-36px)] overflow-hidden relative ">
                      <motion.img
                        src="/images/heroScrolls/scroll-1.jpg"
                        alt="Scrolling image"
                        className="absolute top-0 left-0 w-full h-auto"
                        initial={{ y: 0 }}
                        animate={animateMobile ? { y: 0 } : { y: -1200 }}
                        transition={{
                          duration: 10,
                          ease: "easeInOut",
                          repeat: Infinity,
                          repeatType: "reverse",
                        }}
                      />
                    </Avatar>
                  </div>
                </motion.div>
              </div>
              <div className="item-start   w-full border-b-1 p-4 ">
                <h4
                  className={`lg:text-[20px] max-lg:text-center text-${bg.textColor}`}
                >
                  Trusted by the world's fastest-growing{" "}
                  <span
                    className={`font-semibold text-${bg.textColor} cursor-pointer`}
                  >
                    Startups
                  </span>{" "}
                  and{" "}
                  <span
                    className={`font-semibold text-${bg.textColor} cursor-pointer`}
                  >
                    Enterprises
                  </span>
                </h4>
              </div>
              <div className="flex flex-row flex-wrap  justify-between  w-full">
                {Logos.map((item, index) => {
                  return (
                    <div
                      key={index}
                      className=" flex flex-col max-lg:w-1/2 flex-col items-center    "
                    >
                      <Avatar>
                        <AvatarImage
                          src={item.logo}
                          className=" w-20 h-20 object-contain grayscale "
                          onClick={() => {
                            setBgIndex(index);
                          }}
                        />
                        <AvatarFallback>Logo here</AvatarFallback>
                      </Avatar>
                      <Circle className="w-2 h-2 bg-gray-400 text-gray-200 rounded-[10px]  " />
                    </div>
                  );
                })}
              </div>
            </section>
          );
        }
      })}

      {/* Services */}
      <section className="  flex flex-col items-center justify-center  bg-black ">
        <div className="container  flex justify-center max-lg:p-4 lg:pt-[110px]">
          <div className="upper text-center lg:w-[72%]  space-y-8">
            <h2 className="text-[60px]  max-lg:text-[24px] text-center font-bold leading-tight text-white">
              Websites aren’t just part of what we do. They’re everything we do.
            </h2>
            <p className="text-[#a5acc0] text-center max-lg:text-[14px] text-[20px] leading-[30px]">
              We help B2B marketing teams turn outdated, underperforming
              websites into scalable, high-converting products that engage
              audiences, generate pipeline, and accelerate growth.
            </p>
          </div>
          <div className="lower"></div>
        </div>

        <div className="w-full lg:pt-[200px]">
          <ServiceCard />
        </div>
      </section>

      {/* Stories */}
      <section className="  flex flex-row max-lg:flex-col items-center h-[80vh] max-lg:h-[60vh] px-[5%] justify-evenly  bg-black   ">
        <div className="lg:left lg:w-1/2">
          <h2 className="text-white text-[60px] max-lg:text-[24px] lg:leading-[70px] ">
            Brands you know.
            <br /> Results you’ll love.
          </h2>
        </div>
        <div className="right flex flex-col max-lg:justify-center max-lg:text-center max-lg:items-center ">
          <h4 className="text-[#a5acc0] text-[20px] max-lg:text-[16px]  pb-10">
            Real stories from companies that wanted more—and got it.
          </h4>
          <div>
            <WSButton
              value="View Client Stories"
              icon={
                <ChevronRight className="absolute inset-0 transition-opacity duration-200 opacity-100 group-hover:opacity-0" />
              }
              hovericon={
                <ArrowRight className="absolute inset-0 transition-opacity duration-300 opacity-0 group-hover:opacity-100" />
              }
            />
          </div>
        </div>
      </section>

      

      {/* Experts */}
      <section className="  flex flex-row max-lg:flex-col items-center h-[30vh] max-lg:h-[100vh] px-[5%] justify-evenly bg-black   ">
        <Experts />
      </section>

      {/* industries */}
      <section className="  flex flex-col max-lg:flex-col items-center h-[80vh] max-lg:h-[100vh] px-[5%] justify-evenly bg-black   ">
        <div className="lg:left lg:w-[70%]">
          <h2 className="text-white text-[60px] text-center max-lg:text-[24px] lg:leading-[70px] ">
            Serving industries that demand speed and scale.
          </h2>
        </div>
        <div className="right flex flex-col max-lg:justify-center max-lg:text-center items-center ">
          <h4 className="text-[#a5acc0] text-[20px] max-lg:text-[16px]  pb-10">
            We specialize in industries where high performance matters most.
          </h4>
          <div>
            <WSButton
              value="View All Industries"
              icon={
                <ChevronRight className="absolute inset-0 transition-opacity duration-200 opacity-100 group-hover:opacity-0" />
              }
              hovericon={
                <ArrowRight className="absolute inset-0 transition-opacity duration-300 opacity-0 group-hover:opacity-100" />
              }
            />
          </div>
        </div>
      </section>

      <section className="  flex flex-col  max-lg:flex-col items-center h-[80vh] max-lg:h-[100vh] px-[5%] justify-evenly bg-black   ">
        <Industries />
      </section>

      {/* Team */}
      <section className="  flex flex-row  max-lg:flex-col items-center h-[80vh] max-lg:h-[100vh] px-[5%] justify-evenly bg-black  max-lg:pt-50 ">
        {teamData.map((item, index) => {
          if (teamIndex === index) {
            return (
              <div className="left lg:w-1/2 space-y-6 " key={index}>
                <div className="upper flex lg:flex-row gap-x-3">
                  <div>
                    <Avatar>
                      <AvatarImage
                        src={item.image}
                        className="rounded-[20px] w-10 h-10"
                      />
                      <AvatarFallback>Team Membor</AvatarFallback>
                    </Avatar>
                  </div>
                  <div className="text-white">
                    <h4>{item.name}</h4>
                    <h4>{item.role}</h4>
                  </div>
                </div>
                <div className="lower h-[120px] text-white">
                  <p>{item.description}</p>
                </div>
                <div className="text-white space-x-3">
                  <button
                    className="cursor-pointer"
                    onClick={() => setTeamIndex(2)}
                  >
                    <FaRegArrowAltCircleLeft className=" w-8 h-8" />
                  </button>
                  <button onClick={() => setTeamIndex(3)}>
                    <FaRegArrowAltCircleRight className=" w-8 h-8" />
                  </button>
                </div>
              </div>
            );
          }
        })}
        ,
        <div className="right lg:w-1/2   flex flex-row  justify-center flex-wrap    ">
          {teamData.map((item, index) => {
            return (
              <div
                key={index}
                className={`border   transition-colors duration-300 ease-in-out rounded-[5px] ${
                  teamIndex === index ? "border-[#1663ff]" : "border-[#08090c]"
                }   lg:p-10 max-lg:p-5`}
                onClick={() => setTeamIndex(index)}
              >
                <Avatar>
                  <AvatarImage
                    src={item.icon}
                    className="w-25 h-10 grayscale"
                  />
                  <AvatarFallback>Team Member</AvatarFallback>
                </Avatar>
              </div>
            );
          })}
        </div>
      </section>

      {/* Blog */}
      <section className="  flex flex-col max-lg:flex-col items-center lg:h-[30vh] max-lg:h-[100vh] max-lg:pt-70 px-[5%] justify-evenly bg-black   ">
        <div className="lg:left space-y-10">
          <h2 className="text-white text-[60px] text-center max-lg:text-[24px] lg:leading-[70px] ">
            Check out our latest blog insights
          </h2>
          <h4 className="text-[#a5acc0] text-[20px] max-lg:text-[16px]  pb-10 text-center">
            Stay ahead with expert strategies, trends, and actionable tactics
            from the Webstacks team.
          </h4>
        </div>
      </section>
      {/* Blog Cards */}
      <section className="  flex flex-row  items-center justify-center bg-black ">
        <BlogCard />
      </section>
    </Fragment>
  );
}
